#splash {
	position: fixed;
	width: 100%;
	height: 100%;
	z-index: 999;
	background:#eee;
	text-align:center;
	color:#fff;
}

#splash_logo {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}

#splash_logo svg{
    width:300px;
}

#mask path {
		fill-opacity: 0;
		transition: fill-opacity .5s;
		fill: none;
		stroke: #333;
	}

#mask.done path{
	  fill: #333;
	  fill-opacity: 1;
	  stroke: none;
	}